/*Header*/
.main-bg{
  background-image: url(/netflix-clone/static/media/netflix_bg_1.c086ef53.jpg);
  position: relative;
  background-repeat: no-repeat;
  width: 100%;
  height: 500px;
  z-index: 99;
  background-size:cover;
}
.main-heading{
  display: block;
  background:#000000cf;
  color:#fffbfb;
  position: relative;
  top:20%;
  left:19%;
  border-radius: 2px;
  -webkit-filter:brightness(120%);
          filter:brightness(120%);
  justify-content:space-between;
  align-items: center;
  padding: 70px 20px;
  text-shadow:1px 1px 20px #ffd5d5;
  box-shadow: 0 0 40px #414141;
  font-family: 'Poppins';
  width:61%;
  text-align: center;
}

.main-heading span{
  color:#b30000;
  border-bottom:2px #8a0000;
}
.close{
  position: absolute;
  right:10px;
  top:10px;
  width:32px;
  height: 32px;
  opacity: 0.4;
}
.close:hover{
  opacity: 1;
  cursor: pointer;
}
.close:before, .close:after {
  position: absolute;
  left:15px;
  content: '';
  height: 33px;
  width: 2px;
  background-color: rgb(199, 150, 150);
}
.close:before{
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close::after{
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.main-heading span{
  color:rgb(255, 138, 138)
}
.header-btn{
  background-color: #00000069;
  color: rgb(255, 255, 255);
  border:2px solid #e50914;
  text-decoration: none;
  padding: 5px 5px;
  border-radius: 3px;
  font-size: 0.9rem;
  box-shadow: 2px 2px rgba(34, 19, 19, 0.568);
  cursor: pointer;
}
.header-btn:hover{
  border:2px solid rgb(255, 109, 109);
  text-shadow: 1px 1px 20px #ffd5d5;
}
